<template>
  <div class="reset-psw-view view-background success">
    <header-view :fixed="fixed" :title="title" :routerName="routerName"></header-view>
    <div class="pay-success">
      <div class="pay-success-icon"></div><span>支付成功</span></div>
    <div class="pay-btn">
      <div class="login-in" @click="to('0')">查看订单</div>
      <div class="login-in mt" @click="to('1')">返回主页</div>
    </div>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '支付成功',
        fixed: false,
        routerName: 'custom'
      }
    },
    methods: {
      to: function (val) {
        if (val === '0') {
          this.$router.push({
            name: 'myOrder'
          })
        } else {
          this.$router.push({
            name: 'home'
          })
        }
      }
    }
  }
</script>

<style>
.success .mintui-back{
  display: none;
}
  .pay-success {
    text-align: center;
    color: #333;
  }
  
  .pay-success-icon {
    /*width: 100%;*/
    width: 1rem;
    height: 1rem;
    position: relative;
    margin: 0 auto;
    margin-top: 20%;
    margin-bottom: 5%;
    background: url(/static/image/shopping_cart_list_select@3x.png);
    background-size: 1rem 1rem;
  }
  .pay-btn {
    margin-top: 10%;
  }
  .login-in {
    position: absolute;
    width: 80%;
    height: 7%;
    line-height: .45rem;
    left: 50%;
    margin-top: .3rem;
    text-align: center;
    color: rgb(255,161,38);
    transform: translateX(-50%);
    border-radius: .3rem;
    background:url(/static/image/login/login_bar_dis@3x.png) no-repeat ;
    background-size: 100% 100%;
    color: white;
}
.mt {
  margin-top: 25%;
}
</style>


